.Iam {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	font: normal 40px/50px Montserrat, sans-serif;
	color: #999;
  }
  .Iam p {
	height: 50px;
  }
  .Iam b {
	overflow: hidden;
	position: relative;
	height: 50px;
	top: 10px;
	left: 10%;
  }
  .Iam .innerIam {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	color: #e74c3c;
	position: relative;
	white-space: nowrap;
	top: 10%;
	left: 0;
  
  
  /*animation*/
  -webkit-animation:move 5s;
		  animation:move 5s;
  /*animation-iteration-count*/
  -webkit-animation-iteration-count:infinite;
		  animation-iteration-count:infinite;
  /*animation-delay*/
  -webkit-animation-delay:1s;
		  animation-delay:1s;
  }
  @keyframes move{
  0%  { top: 0px; }
  20% { top: -50px; }
  40% { top: -100px; }
  60% { top: -150px; }
  80% { top: -200px; }
  }
  
  @-webkit-keyframes move {
	  0%  { top: 0px; }
	  20% { top: -50px; }
	  40% { top: -100px; }
	  60% { top: -150px; }
	  80% { top: -200px; }
  }
  @keyframes move {
	  0%  { top: 0px; }
	  20% { top: -50px; }
	  40% { top: -100px; }
	  60% { top: -150px; }
	  80% { top: -200px; }
  }


