#contact {
    width: 100%;
    height: 100%;
  }
  
  .section-header {
    text-align: center;
    margin: 0 auto;
    padding: 40px 0;
    font: 300 60px 'Oswald', sans-serif;
    color: #fff;
    text-transform: uppercase;
    letter-spacing: 6px;
  }
  
  .contact-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 0 auto;
    padding: 20px;
    position: relative;
    max-width: 840px;
  }
  
  /* Left contact page */
  .form-horizontal {
    /*float: left;*/
    max-width: 400px;
    font-family: 'Lato';
    font-weight: 400;
  }
  
  .form-control, 
  textarea {
    max-width: 400px;
    background-color: #444446;
    color: #fff;
    letter-spacing: 1px;
  }
  .form-control{
    height: 50px;
    font-size: 18px;
  }
  
  .send-button {
    margin-top: 15px;
    height: 45px;
    font-size: 20px;
    background-color: hsl(180, 62%, 55%);
    width: 400px;
    overflow: hidden;
    transition: all .2s ease-in-out;
  }
  
  .alt-send-button {
    width: 400px;
    height: 34px;
    transition: all .2s ease-in-out;
  }
  
  .send-text {
    display: block;
    margin-top: 10px;
    font: 700 12px 'Lato', sans-serif;
    letter-spacing: 2px;
  }
  
  .alt-send-button:hover {
    -webkit-transform: translate3d(0px, -29px, 0px);
            transform: translate3d(0px, -29px, 0px);
  }
  
  /* Begin Right Contact Page */
  .direct-contact-container {
    max-width: 400px;
  }
  
  /* Location, Phone, Email Section */
  .contact-list {
    list-style-type: none;
    margin-left: -30px;
    padding-right: 20px;
  }
  
  .list-item {
    line-height: 4;
    color: #aaa;
  }
  
  .contact-text {
    font: 300 18px 'Lato', sans-serif;
    letter-spacing: 1.9px;
  }
  
  .place {
    margin-left: 62px;
  }
  
  .phone {
    margin-left: 56px;
  }
  
  .gmail {
    margin-left: 53px;
  }
  
  .contact-text a {
    color: #bbb;
    text-decoration: none;
    transition-duration: 0.2s;
  }
  
  .contact-text a:hover {
    color: #fff;
    text-decoration: none;
  }
  
  .social-media-list li {
    display: inline-block;
    margin: 20px 10px 10px 0px;
  }
  .social-media-list li a{
    padding: 30px 20px 20px 20px;
    color: honeydew;
    border-radius: 50%;
  }
  .social-media-list li a:hover {
    background-color: honeydew;
    color: black;
    transition: 0.2s ease;
  }
  
  .copyright {
    font: 200 14px 'Oswald', sans-serif;
    color: #555;
    letter-spacing: 1px;
    text-align: center;
  }
  
  hr {
    border-color: rgba(255,255,255,.6);
  }
  
  /* Begin Media Queries*/
  @media screen and (max-width: 850px) {
    .contact-wrapper {
      display: flex;
      flex-direction: column;
    }
    .direct-contact-container, .form-horizontal {
      margin: 0;
    }  
    
    .direct-contact-container {
      margin-top: 60px;
    }    
      .social-media-list li a{
        height: 50px;
        width: 50px;
        line-height: 55px;
        font-size: 2rem;
      }
  }
  
  @media screen and (max-width: 569px) {
  
    .form-wrapper {
      float: none;
      margin: 0 auto;
    }  
    .direct-contact-container{
      margin: 0;
    }
    .form-control, textarea {
      
      margin: 0 auto;
    }
   
    
    .name, .email, textarea {
      width: 280px;
    } 
    
    .direct-contact-container {
      margin-top: 60px;
      max-width: 280px;
    }  
    .social-media-list {
      left: 0;
    }
    .social-media-list li a{
      height: 55px;
      width: 55px;
      line-height: 55px;
      font-size: 2rem;
    }
    .social-media-list li:last-child {
      display: none;
    }
    .contact-list {
      padding-right: 0;
    }
    .contact-list li span {
      margin-left: 10px;
    }

  }
  
  @media screen and (max-width: 410px) {
    .send-button {
      width: 99%;
    }
  }
